  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8em 0em;
}
.logincont .login {
  width: 80%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.logincont .login .titulo {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 4em;
}
.logincont .login .titulo p {
  font-size: 1.3em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-bottom: 0rem;
}
.logincont .login .titulo img {
  width: 50%;
  height: auto;
  margin-bottom: 1em;
}
.logincont .login .titulo .imagensita {
  height: auto;
  width: 100px;
  margin-top: 10px;
  padding-left: 5px;
  padding-right: 5px;
}
.logincont .login .titulo .separator {
  margin-bottom: 15px;
}
.logincont .login .titulo .separator:after {
  width: 70px;
  display: block;
  margin: auto;
  content: "";
  height: 1px;
  background: #000000;
  bottom: 0px;
  left: 50%;
  transition: width 0.8s ease, background-color 0.8s ease;
  -webkit-transition: width 0.8s ease, background-color 0.8s ease;
  -o-transition: width 0.8s ease, background-color 0.8s ease;
  -ms-transition: width 0.8s ease, background-color 0.8s ease;
  -moz-transition: width 0.8s ease, background-color 0.8s ease;
}
.logincont .login .titulo .separator:before {
  content: "";
  display: block;
  margin: auto;
  left: 50%;
  height: 1px;
  width: 50px;
  bottom: -10px;
  background: #000000;
  transition: width 0.8s ease, background-color 0.8s ease;
  -webkit-transition: width 0.8s ease, background-color 0.8s ease;
  -o-transition: width 0.8s ease, background-color 0.8s ease;
  -ms-transition: width 0.8s ease, background-color 0.8s ease;
  -moz-transition: width 0.8s ease, background-color 0.8s ease;
  margin-bottom: 4px;
}
.logincont .login form {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 3em;
}
.logincont .login form .sup {
  width: 50%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-right: 1em;
}
.logincont .login form .sup .texti {
  width: 100%;
  font-size: 1.1em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-bottom: 0rem;
  margin-bottom: 0.5em;
}
.logincont .login form .sup input {
  width: 100%;
  height: 3em;
  background: #f6f6f6;
  border: none;
  color: #999999;
  font-size: 1.1em;
  text-indent: 1em;
  margin-bottom: 1em;
}
.logincont .login form .sup .input:focus {
  outline: none;
}
.logincont .login form .sup .forgot {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 1em;
}
.logincont .login form .sup .forgot a {
  font-size: 1.2em;
  color: #ff334c;
}
.logincont .login form .sup .forgot a:hover {
  animation: parpadeo 0.7s ease infinite;
}
@keyframes parpadeo {
  50% {
    opacity: 0;
  }
}
.logincont .login form .sup button {
  width: 100%;
  height: 3em;
  background: #3cc3f8;
  color: #fff;
  font-size: 1.1em;
  border: none;
  cursor: pointer;
  transition: 1s all ease-in-out;
}
.logincont .login form .sup button:hover {
  background: none;
  border: 2px solid #ff334c;
  color: #ff334c;
}
.logincont .login form .imagen-sesion {
  width: 40%;
  height: auto;
}
.logincont .login form .imagen-sesion img {
  width: 100%;
  height: auto;
}
.logincont .login .logsm {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 1s all ease-in-out;
  padding-bottom: 1em;
}
.logincont .login .logsm h1 {
  font-size: 1.5em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-bottom: 1em;
}
.logincont .login .logsm .redes {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.logincont .login .logsm .redes .red {
  width: 8em;
  padding: 0.5em 0em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: 0.5em;
  cursor: pointer;
  transition: 0.3s all ease-in-out;
}
.logincont .login .logsm .redes .red i {
  color: #fff;
  margin-right: 0.5em;
}
.logincont .login .logsm .redes .red p {
  color: #fff;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-bottom: 0rem;
}
.logincont .login .logsm .redes .red:hover {
  width: 8.5em;
  padding: 0.7em 0em;
}
.logincont .login .logsm .redes .red:nth-child(1) {
  background: #4a70ab;
}
.logincont .login .logsm .redes .red:nth-child(2) {
  background: #00d0da;
}
.logincont .login .logsm .redes .red:nth-child(3) {
  background: #e64d39;
}
.logincont .login .logsm .redes .red:last-child {
  margin-right: 0em;
}
@keyframes rainbow {
  0% {
    border-bottom: 4px solid #4a70ab;
  }
  33% {
    border-bottom: 4px solid #4a70ab;
  }
  66% {
    border-bottom: 4px solid #00d0da;
  }
  100% {
    border-bottom: 4px solid #e64d39;
  }
}
.logincont .login:hover .titulo .separator:before {
  width: 70px;
  left: 0;
  background: #ff334c;
}
.logincont .login:hover .titulo .separator:after {
  width: 50px;
  left: 0;
  background: #ff334c;
